:root {
  --u-box-shadow--normal: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
  --u-color--pink: #fe0096;
  --u-color--white: #fff;
  --u-color--gray-1: #ebeced;
  --u-color--gray-2: #f5f5f5;
  --u-color--black-1: #545454;
  --u-color--black-2: #576778;
  --u-color--yellow: #fbe50d;
}

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Source Sans Pro', Arial, sans-serif;
}

body {
  background: #f7f7f7;
}

a,
u {
  text-decoration: none;
  color: var(--u-color--black-1);
}

button,
input {
  outline: none;
}

button {
  cursor: pointer;
}

.e-button--no-style {
  background-color: transparent;
  border: 0;
}

.error-message {
  font-family: Flavors;
  font-weight: bold;
  color: var(--u-color--pink);
  text-align: center;
  font-size: 3.25rem;
}

.spinner {
  -ms-flex-item-align: center;
      align-self: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.spinner::before {
  content: '';
  width: 50px;
  height: 50px;
  border: 3px solid rgba(255, 205, 255, 0.6);
  border-radius: 50%;
  border-top-color: var(--u-color--pink);
  -webkit-animation: spin 1s ease-in-out infinite;
          animation: spin 1s ease-in-out infinite;
}

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



