.c-table {
  width: 100%;
  -webkit-box-shadow: var(--u-box-shadow--normal);
          box-shadow: var(--u-box-shadow--normal);
  border-collapse: collapse;
  border-spacing: 0;
  background: var(--u-color--white);
}

.c-table thead {
  background: var(--u-color--gray-2);
}

.c-table thead th {
  border: solid 1px var(--u-color--gray-1);
  text-align: left;
}

.c-table tbody tr {
  border: solid 1px var(--u-color--gray-1);
}

.c-table tbody tr:hover {
  background: var(--u-color--gray-1);
  cursor: pointer;
}

.c-movie-list--sort-button {
  width: 100%;
  text-align: left;
  font-size: 1.25rem;
  padding: 8px;
  font-weight: bold;
  cursor: pointer;
  color: var(--u-color--black-2);
}

.sort-enable {
  color: var(--u-color--pink);
}

.c-movie-list--item {
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  height: 65px;
  -ms-flex-align: center;
      align-items: center;
}

.c-movie-list-item--year {
  background: var(--u-color--yellow);
  border-radius: 20px;
  padding: 2px 8px;
}



